.c-participant-nav {
	list-style-type: none;
	display: -ms-flexbox;
	display: flex;
	padding: 0;
	margin: 0;
	-ms-flex: 1 1;
	    flex: 1 1;
}

.c-participant-nav__link {
	-ms-flex: 1 1;
	    flex: 1 1;
	text-align: center;
}

.c-participant-nav .c-participant-nav__link--active-link {
	color: #FD786D;
	border-bottom: 2px solid #FD786D;

}

.c-participant-nav a {
	padding: 1em 0 1em 0;
	text-decoration: none;
	width: 100%;
	display: inline-block;
	font-family: Arial;
	text-transform: uppercase;
	color: #808080;
	font-size: 2.5vh;
}


@media(min-width: 769px) {
	.c-participant-nav .c-participant-nav__link--active-link, .c-participant-nav .c-participant-nav__link--non-active-link {
		padding-bottom: 10px;
	}

	.c-participant-nav {
		-ms-flex: unset;
		    flex: unset;
		-ms-flex-item-align: start;
		    align-self: flex-start;
		-ms-flex-preferred-size: 30%;
		    flex-basis: 30%;
		padding-left: 1em;
	}

}



