.c-goal__header__pending-flag {
	float: right;
	height: 20px;
	width: 95px;
	margin-top: 5px;
	margin-right: 20px;
	background-color: #F2BC16;
}

.c-goal__header__pending-flag-text {
	color: white;
	font-weight: 100;
	text-align: center;
}

.c-goal__category-title {
	color: white;
	font-weight: 500;
	position: relative;
	top: 50%;
	-webkit-transform: translateY(-50%);
	    -ms-transform: translateY(-50%);
	        transform: translateY(-50%); 
	white-space: pre-wrap;
}

.c-goal__category-status {
	color: white;
	font-weight: 100;
	font-size: 0.75em;
	margin-bottom: 5px;
	position: absolute;
	top: 10em;
	bottom: 0%;
	left: 50%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
}

.c-goal__number-of-goals {
	color: white;
	font-weight: 100;
	font-size: 0.75em;
	margin: 0;
	position: relative;
	position: absolute;
	top: 10em;
	bottom: 0%;
	left: 50%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
}

.c-goal__category-info {
	padding-top: 50%;
	position: relative;
}

@media (min-width: 769px) {
	.c-goal {
		/* display: flex; */
		-ms-flex-preferred-size: 29.25%;
		    flex-basis: 29.25%;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
		height: 225px;
		-ms-flex-align: center;
		    align-items: center;
		margin: 2%;
		-webkit-box-shadow: 0 0 15px rgba(0,0,0,0.25);
		        box-shadow: 0 0 15px rgba(0,0,0,0.25);
	}

	.c-goal a {
		margin: auto;
	}
	.c-goal__category-title {
		position: static;
		-webkit-transform: unset;
		    -ms-transform: unset;
		        transform: unset;
		top: unset;
		font-size: 2em;
	}
	.c-goal__category-status {
		position: static;
		-webkit-transform: unset;
		    -ms-transform: unset;
		        transform: unset;
	}
	.c-goal__number-of-goals {
		position: static;
		-webkit-transform: unset;
		    -ms-transform: unset;
		        transform: unset;
		margin-top: 10px;
		font-size: 1em;
	}
	.c-goal__category-info {
		/* padding-top: 0; */
		padding-top: 25%;
		position: static;
	}
}


