.gravatar-link {
    text-align: center;
    margin-top: 1em;
}

@media (max-width: 768px) {
    .c-profile-container {
        margin: 0;
        padding: 0;
    }

    .c-profile-container form {
        margin-top: 8vh;
        background-color: #EBECED;
    }

    .c-profile input {
        background-color: white;
        padding: .5em 1em;
    }
}
.c-profile {
    max-width: 450px;
    margin: 11vh auto 0;
    background-color: white;
    padding: 2.3em;
    -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
    border-radius: 4px;
}

.c-profile .form-control {
    padding: 0;
}

.c-profile .btn {
    display: block; 
    margin: auto;
    margin-top: 1em;
}

.c-profile img {
    display: block;
    margin: 0 auto;
    width: 100%;
    border-radius: 50%;
    max-width: 100px;
}

.c-profile h1 {
    text-align: center;
}

.c-profile input[type=file] {
    border:  none;
    -webkit-box-shadow: none;
            box-shadow: none;
}
.hide {
    display: none;
}

.show {
    display: block;
}

.c-profile input[type=text]:disabled {
    border: none;
    background-color: white;
    -webkit-box-shadow: none;
            box-shadow: none;
    margin:auto;
}

.c-edit-button {
    display: block;
    margin: 2em auto 0;
}

.c-profile h2 {
    text-align: center;
    margin: .3em 0 .6em 0;
}


