.c-progress-nav {
	list-style-type: none;
	display: -ms-flexbox;
	display: flex;
	padding: 0;
	margin: 0;
}

.c-progress-nav__link {
	-ms-flex: 1 1;
	    flex: 1 1;
	text-align: center;
}

.c-progress-nav__link--non-active-link span {
	color: #808080;
}

.c-progress-nav__link--active-link {
	color: #FD786D;
	border-bottom: 2px solid #FD786D;
}

.c-progress-nav__link--active-link span {
	color: #FD786D;

}
.c-progress-nav span {
	padding: 1em 0 1em 0;
	text-decoration: none;
	width: 100%;
	display: inline-block;
	font-family: Arial;
	text-transform: uppercase;
	font-size: 2.5vh;
}

@media(min-width: 769px) {
	.c-progress-nav span {
		padding: .5em 0;
		font-size: 2.5vh;
	}

	.c-progress-nav {
	  padding: 15px;
	  width: 400px;
	  -ms-flex-preferred-size: 30%;
	      flex-basis: 30%;
	}
}




