.c-participant-form .c-participant-search {
	display: -ms-flexbox;
	display: flex;
	margin: .8em 0 0 .7em;
}

@media(min-width: 789px) {
	.c-participant-form .c-participant-search {
		margin-bottom: 1em;
    }
    .c-participant-form {
        -ms-flex-preferred-size: 70%;
            flex-basis: 70%;
        -ms-flex-item-align: end;
            align-self: flex-end;
        margin: 0;
    }
    .c-participant-form .c-participant-search {
        margin: 0 .7em 0 2em;
        width: 50%;
    }
    .c-participant-plus {
        margin-left: 5px;
    }
}


